.footer {
  display: flex;
  width: 75vh;
  transform: rotate(-90deg) translateX(100%);
  transform-origin: right bottom;
  position: absolute;
  bottom: 5vh;
  right: 2.8vw;
  z-index: 3;
}
.footer__menu {
  display: flex;
}
.footer__menu--item {
  padding: 0 16px;
}
.footer__menu--link {
  font-weight: 600;
}